<template>
  <EluiChinaAreaDht
    @change="chinaAreaChange"
    :leave="props.leave"
    :isall="props.isAll"
    :model-value="props.defaultValue"
  ></EluiChinaAreaDht>
</template>
<script setup lang="ts">
const props = withDefaults(
  defineProps<{
    index: number; // 列表的索引值。默认为0
    leave: number;
    isAll?: boolean;
    defaultValue?: string[];
  }>(),
  {
    index: -1,
    leave: 3,
    isAll: false,
    defaultValue: () => [],
  }
);
const emits = defineEmits<{
  (e: "emitFunc", value: Record<string, any>): void;
}>();
import { EluiChinaAreaDht } from "elui-china-area-dht";

const chinaData = new EluiChinaAreaDht.ChinaArea().chinaAreaflat;

function chinaAreaChange(value: string | number[]) {
  if (Array.isArray(value) && value.length) {
    const list = value.reduce((pre, cur) => {
      return pre.concat(chinaData[cur]);
    }, []);
    const nameList = list.map((item) => item.label);
    const data = {
      list,
      nameList,
      valueList: value,
    };
    if (props.index !== -1) {
      Reflect.set(data, "index", props.index);
    }
    emits("emitFunc", data);
  }
}
</script>
